import React, { Fragment } from 'react'
import { Menu, Transition } from '@headlessui/react'
import FilterActions from '../filterActions/FilterActions'

function classNames(...classes) {
  return classes.filter(Boolean).join(' ')
}

const FilterDropDown= ()=> {
  return (
    <Menu as="div" className="relative inline-block text-right">
      <div>
        <Menu.Button className="inline-flex w-full justify-center gap-x-1.5 rounded-md bg-white px-10 py-2 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50">
          Options
          {/* <ChevronDownIcon className="-mr-1 h-5 w-5 text-gray-400" aria-hidden="true" /> */}
        </Menu.Button>
      </div>

      <Transition
        as={Fragment}
        enter="transition ease-out duration-100"
        enterFrom="transform opacity-0 scale-95"
        enterTo="transform opacity-100 scale-100"
        leave="transition ease-in duration-75"
        leaveFrom="transform opacity-100 scale-100"
        leaveTo="transform opacity-0 scale-95"
      >
        <Menu.Items className="absolute right-0 z-10 mt-2 w-56 origin-top-right rounded-md bg-white p-[30px] shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
        <FilterActions />
        </Menu.Items>
      </Transition>
    </Menu>
  )
}

export default React.memo(FilterDropDown);

